import React from 'react'
import { Typography, Fade, Container, Grid, Paper } from '@material-ui/core'
import { makeStyles } from '@material-ui/core/styles'
import OutsidePage from 'src/components/blocks/OutsidePage'
import { Link } from 'react-router-dom'

const useStyles = makeStyles((theme) => ({
  text: {
    marginTop: theme.spacing(4),
    fontFamily: 'Google Sans',
  },
  link: {
    textDecoration: 'none',
    color: theme.palette.primary.main,
    transitionDuration: '0.1s',
    '&:hover': {
      textDecoration: 'underline',
      color: theme.palette.primary.dark,
    },
  },
  cross: {
    textDecoration: 'line-through',
    color: theme.palette.text.hint,
  },
  grid: {
    marginTop: theme.spacing(2),
  },
}))

const useCardStyles = makeStyles((theme) => ({
  root: {
    borderRadius: 8,
    padding: theme.spacing(1.5, 2),
    display: 'flex',
    flexDirection: 'column',
    boxShadow:
      '0px 3px 1px -2px rgb(0 0 0 / 12%), 0px 2px 2px 0px rgb(0 0 0 / 7%), 0px 1px 5px 0px rgb(0 0 0 / 4%)',
  },
  link: {
    textDecoration: 'none',
    color: theme.palette.text.primary,
  },
  title: {
    fontFamily: 'Google Sans',
    fontSize: 24,
    fontWeight: 500,
    marginLeft: theme.spacing(2),
  },
  row: {
    display: 'flex',
    flexDirection: 'row',
    alignItems: 'center',
  },
  icon: {
    width: 32,
    height: 32,
  },
  subtitle: {
    color: theme.palette.text.secondary,
    fontFamily: 'Roboto',
    fontSize: 16,
    marginTop: theme.spacing(1),
  },
}))

const Card: React.FC<{
  to: string
  title: string
  iconSrc: string
  subtitle?: string
  medium?: boolean
  small?: boolean
}> = ({ to, title, iconSrc, subtitle, medium, small }) => {
  const classes = useCardStyles()
  const xs = medium ? 8 : small ? 4 : 12
  const md = medium ? 4 : small ? 2 : 6

  return (
    <Grid item xs={xs} md={md}>
      <a className={classes.link} href={to} target="_blank" rel="noreferrer">
        <Paper className={classes.root} elevation={2}>
          <div className={classes.row}>
            <img className={classes.icon} src={iconSrc} alt={title} />
            <Typography className={classes.title}>{title}</Typography>
          </div>
          {subtitle && (
            <Typography className={classes.subtitle}>{subtitle}</Typography>
          )}
        </Paper>
      </a>
    </Grid>
  )
}

